'use client'

import Link from 'next/link'
import { usePathname } from 'next/navigation'

// Support Hub Tab Configuration - Simplified for better UX
// Consolidated from 8 tabs to 5 for cleaner navigation
const SUPPORT_HUB_TABS = [
  { id: 'overview', label: 'Overview', icon: 'fa-chart-line', href: '/sub-account/support-hub' },
  { id: 'tickets', label: 'Tickets', icon: 'fa-ticket', href: '/sub-account/support-hub/tickets' },
  { id: 'knowledge-base', label: 'Knowledge Base', icon: 'fa-book', href: '/sub-account/support-hub/knowledge-base' },
  { id: 'analytics', label: 'Analytics', icon: 'fa-chart-bar', href: '/sub-account/support-hub/analytics' },
  { id: 'settings', label: 'Settings', icon: 'fa-cog', href: '/sub-account/support-hub/settings' },
]

export default function SupportHubLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const pathname = usePathname()

  // Determine which tab is active based on current path
  const getActiveTab = () => {
    // Exact match for overview
    if (pathname === '/sub-account/support-hub') return 'overview'
    
    // Check sub-paths
    for (const tab of SUPPORT_HUB_TABS) {
      if (tab.href !== '/sub-account/support-hub' && pathname.startsWith(tab.href)) {
        return tab.id
      }
    }
    return 'overview'
  }

  const activeTab = getActiveTab()

  return (
    <div className="space-y-6 -m-4 sm:-m-6 lg:-m-8">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-gray-200 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0">
        {/* Header */}
        <div className="flex items-start justify-between mb-4">
          <div>
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-lg bg-gradient-to-br from-purple-500 to-pink-600 flex items-center justify-center text-white shadow-lg">
                <i className="fa-solid fa-headset text-lg"></i>
              </div>
              <div>
                <div className="flex items-center gap-2">
                  <h1 className="text-2xl font-bold text-gray-900">AI Support Hub</h1>
                  <span className="px-2 py-0.5 bg-purple-100 text-purple-700 text-xs font-medium rounded-full flex items-center gap-1">
                    <i className="fa-solid fa-sparkles text-[10px]"></i>
                    AI-Powered
                  </span>
                </div>
                <p className="text-sm text-gray-500">Intelligent support tickets with AI triage, auto-resolution, and customer insights</p>
              </div>
            </div>
          </div>
          <div className="flex items-center gap-3">
            <button className="px-4 py-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition-colors flex items-center gap-2">
              <i className="fa-solid fa-question-circle"></i>
              <span className="hidden sm:inline">Help</span>
            </button>
            <Link 
              href="/sub-account/support-hub/tickets/new"
              className="px-4 py-2 bg-[var(--theme-secondary)] text-white rounded-lg hover:opacity-90 transition-all flex items-center gap-2 font-medium shadow-sm"
            >
              <i className="fa-solid fa-plus"></i>
              <span>New Ticket</span>
            </Link>
          </div>
        </div>
        
        {/* Tab Navigation - Horizontal scrollable tabs */}
        <nav className="flex items-center gap-1 -mb-px overflow-x-auto pb-px scrollbar-thin scrollbar-thumb-gray-300">
          {SUPPORT_HUB_TABS.map(tab => {
            const isActive = tab.id === activeTab
            return (
              <Link
                key={tab.id}
                href={tab.href}
                className={`py-3 px-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap flex items-center gap-2 ${
                  isActive
                    ? 'border-[var(--theme-secondary)] text-[var(--theme-secondary)]'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <i className={`fa-solid ${tab.icon}`}></i>
                {tab.label}
              </Link>
            )
          })}
        </nav>
      </div>

      {/* Main Content Area - Full width without sidebar */}
      <div className="px-4 sm:px-6 lg:px-8 pb-4 sm:pb-6 lg:pb-8">
        {children}
      </div>
    </div>
  )
}
